:root {
  --font-principal: 'Open Sans', sans-serif;
  --color-verde: #00ac68;
  --color-verde-oscuro: #03995d;
  --color-azul: #3a5aa7;
  --color-blanco: #fff;
  --color-negro: #282e2c;
  --color-amarillo: #ac9b00;
  --color-gris-claro: rgb(241, 241, 241);
  --color-gris-oscuro: rgb(158, 158, 158);
  --color-celeste: #80bdff;
  --color-rojo: rgb(246, 112, 112);
  --color-morado: #7c4dff;
  --color-body: #06241b;
  --color-transparencia: #f1f2f2;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Open Sans', sans-serif !important;
  font-family: var(--font-principal) !important;
  height: 100vh;
}
/* .container {
  max-width: 1400px;
  width: 100%;
  margin: 0 auto;
} */

.link {
  text-decoration: none;
  color: #fff;
  color: var(--color-blanco);
}
.btn {
  border: none;
  padding: 0.5rem 3rem;
  border-radius: 5px;
  margin-top: 2rem;
  cursor: pointer;
}
.btn-azul {
  background-color: #80bdff;
  background-color: var(--color-celeste);
  color: #fff;
  color: var(--color-blanco);
}
.btn-azul:hover {
  background-color: #3a5aa7;
  background-color: var(--color-azul);
  color: #fff;
  color: var(--color-blanco);
}
.title {
  text-align: center;
  text-transform: uppercase;
  font-weight: 800;
  font-size: 2.5rem;
}

.sk-cube-grid {
  width: 40px;
  height: 40px;
  margin: 100px auto;
}

.sk-cube-grid .sk-cube {
  width: 33%;
  height: 33%;
  background-color: #80bdff;
  background-color: var(--color-celeste);
  float: left;
  animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
}
.sk-cube-grid .sk-cube1 {
  animation-delay: 0.2s;
}
.sk-cube-grid .sk-cube2 {
  animation-delay: 0.3s;
}
.sk-cube-grid .sk-cube3 {
  animation-delay: 0.4s;
}
.sk-cube-grid .sk-cube4 {
  animation-delay: 0.1s;
}
.sk-cube-grid .sk-cube5 {
  animation-delay: 0.2s;
}
.sk-cube-grid .sk-cube6 {
  animation-delay: 0.3s;
}
.sk-cube-grid .sk-cube7 {
  animation-delay: 0s;
}
.sk-cube-grid .sk-cube8 {
  animation-delay: 0.1s;
}
.sk-cube-grid .sk-cube9 {
  animation-delay: 0.2s;
}

@keyframes sk-cubeGridScaleDelay {
  0%,
  70%,
  100% {
    transform: scale3D(1, 1, 1);
  }
  35% {
    transform: scale3D(0, 0, 1);
  }
}

